// three thumbnails across
@media screen 
	and (min-width: 480px)
	and (max-width: 600px) {
	
	#thumbnail-frame {
		margin: 0 7% 10px;	
		.thumbnail {
			width: 29%;
			margin: 2%;
		}
	}

	#viewer-frame {
		margin: 0 7% 10px;
	}
}

// two thumbnails across
@media screen 
	and (max-width: 480px) {
	
	#thumbnail-frame {
		margin: 0 5% 10px;	
		.thumbnail {
			width: 40%;
			margin: 5%;
		}
	}

	#viewer-frame {
		margin: 0 5% 10px;
	}
}
